/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      site {
        siteMetadata {
          author {
            name
            summary
          }
          social {
            linkedin
            github
            strava
          }
        }
      }
    }
  `)

  // Set these values by editing "siteMetadata" in gatsby-config.js
  const author = data.site.siteMetadata?.author
  const social = data.site.siteMetadata?.social

  return (
    <div className="bio">
      <StaticImage
        className="bio-avatar"
        layout="fixed"
        formats={["auto", "webp", "avif"]}
        src="../images/profile-pic.png"
        width={50}
        height={50}
        quality={95}
        alt="Profile picture"
      />
      {author?.name && (
        <p>
          Howdy ! I am <strong>{author.name}</strong>, {author?.summary || null}
            {` `}
            <br/>
            Follow me here
            <br/>
            <a href={`https://www.linkedin.com/${social?.linkedin || ``}`} className='social-logo'>
                <StaticImage
                    layout="fixed"
                    formats={["auto", "webp", "avif"]}
                    src="../images/linkedin-logo.png"
                    width={32}
                    height={32}
                    quality={95}
                    alt="LinkedIn profile"
                />
            </a>
            <a href={`https://github.com/${social?.github || ``}`} className='social-logo'>
                <StaticImage
                    layout="fixed"
                    formats={["auto", "webp", "avif"]}
                    src="../images/github-logo.png"
                    width={32}
                    height={32}
                    quality={95}
                    alt="Github profile"
                />
            </a>
            <a href={`https://www.strava.com/athletes/${social?.strava || ``}`} className='social-logo'>
                <StaticImage
                    layout="fixed"
                    formats={["auto", "webp", "avif"]}
                    src="../images/strava-logo.png"
                    width={32}
                    height={32}
                    quality={95}
                    alt="Strava profile"
                />
            </a>
            <a href={`https://www.linkedin.com/${social?.linkedin || ``}`} id='linkedin-logo' className='logo'/>
            <a href={`https://github.com/${social?.github || ``}`} id='github-logo' className='logo'/>
            <a href={`https://www.strava.com/athletes/${social?.strava || ``}`} id='strava-logo' className='logo'/>

        </p>
      )}
    </div>
  )
}

export default Bio
